<template>
    <div>
    <el-table
            :data="tableData"
            border
            style="width: 100%">
        <el-table-column
                fixed
                prop="id"
                label="编号"
                width="300px">
        </el-table-column>
        <el-table-column
                prop="bookName"
                label="书名"
                width="300px">
        </el-table-column>
        <el-table-column
                prop="author"
                label="作者"
                width="300px">
        </el-table-column>
        <el-table-column
                prop="price"
                label="价格"
                width="300px">
        </el-table-column>
        <el-table-column label="图片" prop="picturePath">
            <template slot-scope="scope">
                <img :src="scope.row.pic" style="width: 40px;height: 30px">
            </template>
        </el-table-column>
        <el-table-column
                fixed="right"
                label="操作"
                width="300px">
            <template slot-scope="scope">
                <el-button type="text" size="small" @click="edit(scope.row)">修改</el-button>
                <el-button type="text"  size="small" @click="del(scope.row)">删除</el-button>
            </template>
        </el-table-column>
    </el-table>
    <el-pagination
            background
            layout="sizes,prev, pager, next,total"
            :page-size="5"
            :page-sizes="[5,10,15]"
            :total="total"
            @current-change="page"
            @size-change="sizeChange"
            >
    </el-pagination>
    </div>
</template>

<script>
    import PageTwo from "./PageTwo";

    export default {
        components: {PageTwo},
        data(){
            return {
                total:null,
                tableData:[
                    this.id= "",
                    this.bookName="",
                    this.author="",
                    this.price="",
                    this.pic=""
                ],
                SizePage:5,
                currentPage:1
            }
        },
        methods: {
            del(row){
                this.axios({
                    url:'http://localhost:8181/book/deleteBook/'+row.id,
                    method:'post',
                }).then(res=>{
                    this.$message.success("删除成功")
                })
            },
            edit(row){
                this.$router.push({
                    path: '/PageThree',
                    query:{
                        id: row.id
                    }
                })
            },
            page(currentPage){
                this.currentPage=currentPage;
                this.getData();
            },
            sizeChange(SizePage){
                this.SizePage=SizePage;
                this.getData()
            },
            getData(){
                axios.get('http://localhost:8181/book/findAllBooks/'+this.currentPage+'/'+this.SizePage).then(resp =>{
                    this.tableData=resp.data.entity
                    //console.log(resp.data.entity[2].picturePath)
                    this.total=resp.data.total
                })
            }
        },

        created(){
            // const _this=this
            // axios.get('http://localhost:8181/User/findAll/'+this.data.currentPage+'/'+this.data.SizePage).then(function (resp) {
            //     //console.log(resp.data)
            //     console.log(this.data.currentPage)
            //     console.log(this.data.SizePage)
            //     _this.tableData=resp.data.entiy
            //     _this.total=resp.data.total
            // })
            this.getData()
        },
    }

</script>
