<template>
    <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="120px" class="demo-ruleForm">
        <el-form-item label="编号">
            <el-input v-model="ruleForm.id" readonly></el-input>
        </el-form-item>
        <el-form-item label="书名" prop="bookName">
            <el-input v-model="ruleForm.bookName"></el-input>
        </el-form-item>
        <el-form-item label="作者" prop="author">
            <el-input v-model="ruleForm.author"></el-input>
        </el-form-item>
        <el-form-item label="价格" prop="price">
            <el-input v-model="ruleForm.price"></el-input>
        </el-form-item>
        <el-form-item label="图片" prop="pic">
            <el-input v-model="ruleForm.pic"></el-input>
            <page-four @onUpload="uploadImg" ref="PageFour"></page-four>
        </el-form-item>
        <el-form-item>
            <el-button type="primary" @click="update()">提交</el-button>
            <el-button @click="resetForm('ruleForm')">重置</el-button>
        </el-form-item>
    </el-form>
</template>

<script>
    import PageFour from './PageFour'
    export default {
        components: {PageFour},
        data() {
            return {
                ruleForm: {
                    id:'',
                    bookName: '',
                    author:'',
                    price: '',
                    pic: ''
                },
                rules: {
               /*     username: [
                        { required: true, message: '请输入姓名', trigger: 'blur' },
                        { min: 1, max: 255, message: '不能超过255个字符', trigger: 'blur' }
                    ],
                    password: [
                        { required: true, message: '请输入密码', trigger: 'blur' },
                        { min: 1, max: 10, message: '长度要在3~10', trigger: 'blur' }
                    ],
                    age: [
                        { required: true, message: '请输入年龄', trigger: 'blur' },
                        { min: 1, max: 3, message: '不能活太久', trigger: 'blur' }
                    ]*/
                }
            };
        },
        methods: {
            uploadImg () {
                this.ruleForm.pic = this.$refs.PageFour.url
                alert(this.ruleForm.pic)
            },
            update(){
                const _this=this
              this.axios({
                  url:"http://localhost:8181/book/updateBook",
                  method:'post',
                  data:{
                      id: this.ruleForm.id,
                      bookName: this.ruleForm.bookName,
                      author: this.ruleForm.author,
                      price: this.ruleForm.price,
                      pic: this.ruleForm.pic,
                  }
              }).then(res=>{
                  this.$message.success("修改成功")
              })


            },
            resetForm(formName) {
                this.$refs[formName].resetFields();
            }
        },
        created(){
            const _this=this
            axios.get('http://localhost:8181/book/findBookById/'+this.$route.query.id).then(function(resp){
                _this.ruleForm=resp.data

            })

        }
    }
</script>
